@import url(https://fonts.googleapis.com/css?family=Knewave);
/*Fancy toggle button made by http://nathan.tokyo in this pen: https://codepen.io/nathantaylor/pen/jyVeYp */
/*Metalic buttons from https://codepen.io/simurai/pen/DwJdq */
body {
  font-family: "Palatino Linotype", sans-serif;
  background-color: #e6e6e6; }

#drum-machine {
  border: silver;
  border-radius: 0.25rem;
  box-shadow: 20px 20px 50px grey; }

.cent {
  position: absolute !important;
  left: 50% !important;
  top: 50% !important;
  -webkit-transform: translate(-50%, -50%) !important;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

.grid-container {
  width: 50vw;
  display: grid;
  grid-auto-columns: 1fr;
  grid-auto-rows: auto auto auto auto auto;
  grid-gap: 10px;
  background-color: whitesmoke;
  padding: 10px; }

.grid-container > div, .grid-container > button {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  max-height: 100vh; }

button:focus {
  outline: 0; }

#display {
  font-family: "Knewave" !important;
  font-size: 1.5em !important;
  color: whitesmoke; }

.vol1 {
  font-family: "Knewave" !important;
  font-weight: normal !important;
  color: #5cb85c;
  transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -moz-transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -webkit-transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95); }

.vol2 {
  font-family: "Knewave" !important;
  font-weight: normal !important;
  color: #f0ad4e;
  transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -moz-transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -webkit-transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95); }

.vol3 {
  font-family: "Knewave" !important;
  font-weight: normal !important;
  color: #d9534f;
  transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -moz-transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -webkit-transition: all 1500ms cubic-bezier(0.445, 0.05, 0.55, 0.95); }

.item1 {
  grid-column: 1 / 4;
  height: 80px !important;
  line-height: 40px !important;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  color: #5bc0de;
  text-stroke-width: 1px;
  text-stroke-color: gray;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: gray; }

.drum-pad {
  background-color: #f0ad4e !important; }

.item2 {
  grid-column: 2 / 3;
  font-size: 20px !important;
  height: 60px !important;
  padding-top: 0px !important;
  line-height: 25px !important;
  background-color: whitesmoke !important;
  margin-left: auto;
  margin-right: auto; }

.slider {
  margin-top: 15px;
  -webkit-appearance: none;
  width: 100%;
  height: 20px;
  background: #d3d3d3;
  outline: none; }

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 35px;
  height: 25px;
  background: #5cb85c;
  border: 1px dashed #4cae4c;
  cursor: pointer;
  border-radius: 13px; }

.slider::-moz-range-thumb {
  width: 25px;
  height: 20px;
  background: #5cb85c;
  border: 1px dashed #4cae4c;
  cursor: pointer;
  border-radius: 13px; }

.item3 {
  text-align: center !important;
  height: 60px;
  background-color: whitesmoke !important;
  border-radius: 0.25rem; }

.switchOn, .switchOff {
  width: 50%;
  height: 100%;
  font-size: 20px;
  line-height: 50px;
  transition: all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -webkit-transition: all 500ms cubic-bezier(0.445, 0.05, 0.55, 0.95); }

.switchOn {
  background-color: #a6d8a6;
  color: #5bc0de; }

.switchOff {
  background-color: #d9534f;
  color: #f0ad4e;
  float: right; }

@media only screen and (max-width: 768px) {
  .grid-container {
    width: 100vw !important; }
  .grid-container > div:not(.item2) {
    font-size: 100% !important; }
  .item2 {
    font-size: 1em !important; } }

@media only screen and (max-width: 560px) {
  .item2 {
    font-size: 0.8em !important; } }

#switch {
  top: -10px;
  margin-left: auto;
  margin-right: auto;
  outline: none;
  display: block;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
  width: 6rem;
  height: 2rem;
  background: #2e426b;
  border-radius: 2rem;
  -webkit-filter: blur(0.4px);
  filter: blur(0.4px);
  box-shadow: -0.05rem -0.05rem 0.08rem rgba(0, 0, 51, 0.6), 0.05rem 0.05rem 0.1rem #b6fbe4, inset 0.5rem 0.5rem 2rem rgba(0, 0, 51, 0.5), inset 0.1rem 0.1rem 0.2rem rgba(0, 0, 51, 0.75), inset -0.5rem -0.5rem 4rem rgba(182, 251, 228, 0.5); }

.wrap {
  top: -10px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
  width: 6rem;
  height: 2rem;
  border-radius: 2rem;
  overflow: hidden;
  z-index: 1;
  -webkit-transform: translateY(-100%);
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%); }

label {
  cursor: pointer !important;
  display: block;
  position: absolute;
  top: 50%;
  left: 0.1rem;
  width: 4rem;
  height: 1.8rem;
  background: #abbaba;
  border-radius: 2rem;
  -webkit-transform: translate3d(0%, -50%, 0);
  -webkit-transform: translate3d(0%, -50%, 0);
  transform: translate3d(0%, -50%, 0);
  transition: transform 150ms cubic-bezier(0.55, 0.085, 0.68, 0.53), -webkit-transform 150ms cubic-bezier(0.55, 0.085, 0.68, 0.53);
  -webkit-filter: blur(0.4px);
  filter: blur(0.4px);
  box-shadow: inset -0.05rem -0.05rem 0.08rem rgba(0, 0, 51, 0.6), inset 0.05rem 0.05rem 0.1rem #b6fbe4, 0.5rem 0.5rem 2rem rgba(0, 0, 51, 0.5), 0.1rem 0.1rem 0.2rem rgba(0, 0, 51, 0.75); }

#switch:checked + div label {
  -webkit-transform: translate3d(1.85rem, -50%, 0);
  -webkit-transform: translate3d(1.85rem, -50%, 0);
  transform: translate3d(1.85rem, -50%, 0); }

.rib {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0.35rem;
  height: 1.2rem;
  border-radius: 1rem;
  background: #abbaba;
  opacity: 1;
  -webkit-transform: translate3d(-50%, -50%, 0);
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  box-shadow: inset -0.02rem -0.03rem 0.08rem rgba(0, 0, 51, 0.5), inset 0.03rem 0.03rem 0.1rem #b6fbe4, inset -0.02rem -0.03rem 0.2rem rgba(0, 0, 51, 0.3), inset 0.03rem 0.03rem 0.2rem rgba(182, 251, 228, 0.8), 0.05rem 0.08rem 0.3rem rgba(0, 0, 51, 0.3), -0.05rem -0.08rem 0.4rem rgba(182, 251, 228, 0.3), 0rem 0rem 0.2rem rgba(46, 66, 107, 0.2); }

.rib:nth-child(1) {
  left: 35%; }

.rib:nth-child(3) {
  left: 65%; }

.sig {
  position: fixed;
  bottom: 8px;
  right: 8px;
  text-decoration: none;
  font-size: 12px;
  font-weight: 100;
  font-family: sans-serif;
  color: rgba(0, 0, 0, 0.4);
  letter-spacing: 2px; }

.metal {
  cursor: pointer;
  position: relative;
  outline: none;
  text-align: center;
  color: #333333;
  text-shadow: rgba(102, 102, 102, 0.5) 0 -1px 0, rgba(255, 255, 255, 0.6) 0 2px 1px;
  background-color: #e6e6e6;
  box-shadow: inset #262626 0 0px 0px 4px, inset rgba(38, 38, 38, 0.8) 0 -1px 5px 4px, inset rgba(0, 0, 0, 0.25) 0 -1px 0px 7px, inset rgba(255, 255, 255, 0.7) 0 2px 1px 7px, rgba(0, 0, 0, 0.15) 0 -5px 6px 4px, rgba(255, 255, 255, 0.5) 0 5px 6px 4px;
  /* outer HL */
  transition: color .2s; }

/* Radial ------------------------- */
.radial.metal {
  border-radius: 80px;
  background-image: -webkit-radial-gradient(50% 0%, 8% 50%, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%), -webkit-radial-gradient(50% 100%, 12% 50%, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0) 100%), -webkit-radial-gradient(0% 50%, 50% 7%, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%), -webkit-radial-gradient(100% 50%, 50% 5%, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%), -webkit-repeating-radial-gradient(50% 50%, 100% 100%, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 3%, rgba(0, 0, 0, 0.1) 3.5%), -webkit-repeating-radial-gradient(50% 50%, 100% 100%, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 6%, rgba(255, 255, 255, 0.1) 7.5%), -webkit-repeating-radial-gradient(50% 50%, 100% 100%, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 1.2%, rgba(255, 255, 255, 0.2) 2.2%), -webkit-radial-gradient(50% 50%, 200% 50%, #e6e6e6 5%, #d9d9d9 30%, #999999 100%); }

.metal.radial:before, .metal.radial:after {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
  border-radius: inherit;
  /* fake conical gradients */
  background-image: -webkit-radial-gradient(50% 0%, 10% 50%, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%), -webkit-radial-gradient(50% 100%, 10% 50%, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%), -webkit-radial-gradient(0% 50%, 50% 10%, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%), -webkit-radial-gradient(100% 50%, 50% 6%, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%); }

.metal.radial:before {
  -webkit-transform: rotate(65deg);
          transform: rotate(65deg); }

.metal.radial:after {
  -webkit-transform: rotate(-65deg);
          transform: rotate(-65deg); }

/* Linear ------------------------- */
.metal.linear {
  border-radius: .5em;
  background-image: -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 6%, rgba(255, 255, 255, 0.1) 7.5%), -webkit-repeating-linear-gradient(left, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 4%, rgba(0, 0, 0, 0.03) 4.5%), -webkit-repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 1.2%, rgba(255, 255, 255, 0.15) 2.2%), linear-gradient(180deg, #c7c7c7 0%, #e6e6e6 47%, #c7c7c7 53%, #b3b3b3 100%); }

.mt1 {
  font-size: 1.5em !important;
  width: 100px;
  height: 80px;
  border: none;
  display: block;
  text-decoration: none;
  margin-top: 4px;
  margin-left: auto;
  margin-right: auto; }

.btnActive {
  color: #0066cc;
  text-shadow: rgba(0, 51, 102, 0.3) 0 -1px 0, #b3d9ff 0 2px 1px, #99ddff 0 0 5px, rgba(0, 128, 255, 0.6) 0 0 20px;
  box-shadow: inset #004d99 0 0px 0px 4px, inset rgba(0, 38, 77, 0.4) 0 -1px 5px 4px, inset rgba(0, 51, 102, 0.25) 0 -1px 0px 7px, inset rgba(255, 255, 255, 0.7) 0 2px 1px 7px, rgba(128, 191, 255, 0.8) 0 0px 3px 2px, rgba(51, 102, 153, 0.25) 0 -5px 6px 4px, #e8f2fc 0 5px 6px 4px;
  /* outer HL */ }

.btnOn {
  color: #14ff14;
  text-shadow: rgba(92, 183, 92, 0.3) 0 -1px 0, #cae8ca 0 2px 1px, #b8e0b8 0 0 5px, rgba(78, 177, 78, 0.6) 0 0 20px;
  box-shadow: inset #2e6b2e 0 0px 0px 4px, inset rgba(23, 54, 23, 0.4) 0 -1px 5px 4px, inset rgba(31, 71, 31, 0.25) 0 -1px 0px 7px, inset rgba(255, 255, 255, 0.7) 0 2px 1px 7px, rgba(166, 217, 166, 0.8) 0 0px 3px 2px, rgba(61, 143, 61, 0.25) 0 -5px 6px 4px, #edf7ed 0 5px 6px 4px;
  /* outer HL */ }

.btnOff {
  color: #cc2200;
  text-shadow: rgba(102, 17, 0, 0.3) 0 -1px 0, #b3d9ff 0 2px 1px, #99ddff 0 0 5px, rgba(255, 43, 0, 0.6) 0 0 20px;
  box-shadow: inset #991a00 0 0px 0px 4px, inset rgba(77, 13, 0, 0.4) 0 -1px 5px 4px, inset rgba(102, 17, 0, 0.25) 0 -1px 0px 7px, inset rgba(255, 255, 255, 0.7) 0 2px 1px 7px, rgba(255, 149, 128, 0.8) 0 0px 3px 2px, rgba(153, 68, 51, 0.25) 0 -5px 6px 4px, #fcebe8 0 5px 6px 4px;
  /* outer HL */ }

.metal.linear.oval {
  width: 50px;
  height: 50px;
  line-height: 7px !important;
  border-radius: 50%; }

.no {
  align-items: flex-start;
  text-align: center;
  cursor: default;
  color: buttontext;
  background-color: buttonface;
  box-sizing: border-box;
  padding: 2px 6px 3px;
  border-width: 2px;
  border-style: outset;
  border-color: buttonface;
  border-image: none;
  border-image: initial;
  text-rendering: auto;
  color: initial;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  margin: 0em;
  font: 400 13.3333px Arial; }

